/* Tailwind-like utility classes for Renoir */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
  background-color: #fff;
  color: #111827;
  line-height: 1.5;
}
@keyframes spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

@keyframes shimmer {
  0% { background-position: -200% 0; }
  100% { background-position: 200% 0; }
}

.spinner {
  animation: spin 1s linear infinite;
}

.shimmer {
  background: linear-gradient(90deg, #f3f4f6 25%, #e5e7eb 50%, #f3f4f6 75%);
  background-size: 200% 100%;
  animation: shimmer 1.5s infinite;
}
/* Layout utilities */
.min-h-screen { min-height: 100vh; }
.max-w-7xl { max-width: 80rem; }
.mx-auto { margin-left: auto; margin-right: auto; }
.p-4 { padding: 1rem; }
.p-6 { padding: 1.5rem; }
.px-4 { padding-left: 1rem; padding-right: 1rem; }
.py-8 { padding-top: 2rem; padding-bottom: 2rem; }
.py-12 { padding-top: 3rem; padding-bottom: 3rem; }
.mb-2 { margin-bottom: 0.5rem; }
.mb-4 { margin-bottom: 1rem; }
.mb-6 { margin-bottom: 1.5rem; }
.mt-1 { margin-top: 0.25rem; }
.mt-2 { margin-top: 0.5rem; }
.space-y-1 > * + * { margin-top: 0.25rem; }
.space-y-2 > * + * { margin-top: 0.5rem; }
.space-y-4 > * + * { margin-top: 1rem; }

/* Background colors */
.bg-white { background-color: white; }
.bg-gray-50 { background-color: #f9fafb; }
.bg-gray-100 { background-color: #f3f4f6; }
.bg-gray-200 { background-color: #e5e7eb; }
.bg-blue-50 { background-color: #eff6ff; }
.bg-blue-100 { background-color: #dbeafe; }
.bg-blue-500 { background-color: #3b82f6; }
.bg-green-50 { background-color: #f0fdf4; }
.bg-green-100 { background-color: #dcfce7; }
.bg-yellow-50 { background-color: #fefce8; }
.bg-yellow-100 { background-color: #fef3c7; }
.bg-red-50 { background-color: #fef2f2; }
.bg-red-100 { background-color: #fee2e2; }
.bg-purple-50 { background-color: #faf5ff; }
.bg-purple-100 { background-color: #e9d5ff; }
.bg-black { background-color: black; }
.bg-opacity-50 { opacity: 0.5; }
.bg-opacity-80 { opacity: 0.8; }
.bg-opacity-90 { opacity: 0.9; }

/* Text colors */
.text-gray-400 { color: #9ca3af; }
.text-gray-500 { color: #6b7280; }
.text-gray-600 { color: #4b5563; }
.text-gray-700 { color: #374151; }
.text-gray-800 { color: #1f2937; }
.text-gray-900 { color: #111827; }
.text-blue-800 { color: #1e40af; }
.text-green-800 { color: #166534; }
.text-yellow-800 { color: #854d0e; }
.text-red-500 { color: #ef4444; }
.text-red-800 { color: #991b1b; }
.text-purple-800 { color: #6b21a8; }

/* Text utilities */
.text-xs { font-size: 0.75rem; }
.text-sm { font-size: 0.875rem; }
.text-xl { font-size: 1.25rem; }
.text-2xl { font-size: 1.5rem; }
.text-3xl { font-size: 1.875rem; }
.font-medium { font-weight: 500; }
.font-semibold { font-weight: 600; }
.font-bold { font-weight: 700; }
.text-center { text-align: center; }
.truncate { 
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

/* Border utilities */
.border-2 { border-width: 2px; }
.border-3 { border-width: 3px; }
.border-b { border-bottom-width: 1px; }
.border-gray-200 { border-color: #e5e7eb; }
.border-t-blue-500 { border-top-color: #3b82f6; }
.rounded { border-radius: 0.25rem; }
.rounded-lg { border-radius: 0.5rem; }
.rounded-full { border-radius: 9999px; }

/* Display utilities */
.flex { display: flex; }
.grid { display: grid; }
.hidden { display: none; }
.gap-2 { gap: 0.5rem; }
.gap-4 { gap: 1rem; }
.gap-6 { gap: 1.5rem; }
.grid-cols-2 { grid-template-columns: repeat(2, minmax(0, 1fr)); }
.grid-cols-3 { grid-template-columns: repeat(3, minmax(0, 1fr)); }
.col-span-1 { grid-column: span 1 / span 1; }
.col-span-2 { grid-column: span 2 / span 2; }
.flex-1 { flex: 1 1 0%; }
.flex-col { flex-direction: column; }
.items-center { align-items: center; }
.items-start { align-items: flex-start; }
.justify-between { justify-content: space-between; }
.justify-center { justify-content: center; }

/* Position utilities */
.relative { position: relative; }
.absolute { position: absolute; }
.fixed { position: fixed; }
.sticky { position: sticky; }
.inset-0 { top: 0; right: 0; bottom: 0; left: 0; }
.top-0 { top: 0; }
.top-1 { top: 0.25rem; }
.right-1 { right: 0.25rem; }
.z-50 { z-index: 50; }
.overflow-hidden { overflow: hidden; }
.overflow-y-auto { overflow-y: auto; }

/* Size utilities */
.w-full { width: 100%; }
.w-8 { width: 2rem; }
.w-12 { width: 3rem; }
.w-24 { width: 6rem; }
.w-48 { width: 12rem; }
.h-2 { height: 0.5rem; }
.h-8 { height: 2rem; }
.h-12 { height: 3rem; }
.h-24 { height: 6rem; }
.h-48 { height: 12rem; }
.h-auto { height: auto; }
.max-h-80vh { max-height: 80vh; }
.object-cover { object-fit: cover; }

/* Interactive utilities */
.cursor-pointer { cursor: pointer; }
.transition-all { transition: all 150ms; }
.transition-transform { transition: transform 150ms; }
.transition-opacity { transition: opacity 0.3s; }
.hover\:scale-105:hover { transform: scale(1.05); }
.hover\:border-blue-400:hover { border-color: #60a5fa; }
.hover\:text-gray-700:hover { color: #374151; }
.opacity-30 { opacity: 0.3; }
.opacity-50 { opacity: 0.5; }
.opacity-100 { opacity: 1; }

/* List utilities */
.list-disc { list-style-type: disc; }
.list-inside { list-style-position: inside; }

/* Margin and padding utilities */
.ml-4 { margin-left: 1rem; }
.px-1 { padding-left: 0.25rem; padding-right: 0.25rem; }
.px-2 { padding-left: 0.5rem; padding-right: 0.5rem; }
.py-0\.5 { padding-top: 0.125rem; padding-bottom: 0.125rem; }
.py-1 { padding-top: 0.25rem; padding-bottom: 0.25rem; }
.p-2 { padding: 0.5rem; }
.p-3 { padding: 0.75rem; }

/* Components */
.shadow-md {
  box-shadow: 0 4px 6px -1px rgb(0 0 0 / 0.1), 0 2px 4px -2px rgb(0 0 0 / 0.1);
}

.btn-primary, .btn-secondary {
  display: inline-block;
  padding: 0.5rem 1rem;
  border-radius: 0.375rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 150ms;
  border: none;
  font-size: 0.875rem;
}

.btn-primary {
  background-color: #3b82f6;
  color: white;
}

.btn-primary:hover {
  background-color: #2563eb;
}

.btn-secondary {
  background-color: #6b7280;
  color: white;
}

.btn-secondary:hover {
  background-color: #4b5563;
}

/* Responsive grid */
@media (min-width: 768px) {
  .md\:grid-cols-6 { grid-template-columns: repeat(6, minmax(0, 1fr)); }
  .md\:h-28 { height: 7rem; }
}

@media (min-width: 1024px) {
  .lg\:grid-cols-8 { grid-template-columns: repeat(8, minmax(0, 1fr)); }
  .lg\:h-32 { height: 8rem; }
}

@media (min-width: 1280px) {
  .xl\:grid-cols-10 { grid-template-columns: repeat(10, minmax(0, 1fr)); }
}

/* ===== CUSTOM STYLES MOVED FROM App.js ===== */

@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:wght@400;500;600;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600&display=swap');

:root {
  --grid-unit: 8px;
  --color-bg: #FAFAF8;
  --color-white: #FFFFFF;
  --color-black: #1A1A1A;
  --color-gray-dark: #404040;
  --color-gray-medium: #808080;
  --color-gray-light: #E5E5E5;
  --color-border: #E0E0E0;
  --color-accent: #000000;
}

* {
  box-sizing: border-box;
}

body {
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, sans-serif;
  font-weight: 400;
  color: var(--color-black);
  line-height: calc(var(--grid-unit) * 3);
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Playfair Display', Georgia, serif;
  font-weight: 400;
  letter-spacing: -0.02em;
  line-height: 1.2;
}

.container {
  max-width: 1440px;
  margin: 0 auto;
  padding: calc(var(--grid-unit) * 6) calc(var(--grid-unit) * 4);
}

@media (min-width: 768px) {
  .container {
    padding: calc(var(--grid-unit) * 8) calc(var(--grid-unit) * 6);
  }
}

@media (min-width: 1024px) {
  .container {
    padding: calc(var(--grid-unit) * 10) calc(var(--grid-unit) * 8);
    padding-top: 20px;
  }
}

.header-section {
  /* margin-bottom: calc(var(--grid-unit) * 8); */
  text-align: center;
  /* border-bottom: 1px solid var(--color-border);
  padding-bottom: calc(var(--grid-unit) * 6); */
}

.main-title {
  font-size: calc(var(--grid-unit) * 6);
  margin-bottom: calc(var(--grid-unit) * 2);
  font-weight: 400;
  letter-spacing: -0.03em;
}

.subtitle {
  font-family: 'Inter', sans-serif;
  font-size: calc(var(--grid-unit) * 2);
  font-weight: 300;
  color: var(--color-gray-dark);
  letter-spacing: 0.02em;
  text-transform: uppercase;
}

.section {
  background: var(--color-white);
  border: 1px solid var(--color-border);
  margin-bottom: calc(var(--grid-unit) * 4);
  padding: calc(var(--grid-unit) * 4);
}

.section-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: calc(var(--grid-unit) * 4);
  padding-bottom: calc(var(--grid-unit) * 2);
  border-bottom: 1px solid var(--color-border);
}

.section-title {
  font-size: calc(var(--grid-unit) * 3);
  font-weight: 500;
}

.item-count {
  font-family: 'Inter', sans-serif;
  font-size: calc(var(--grid-unit) * 1.75);
  color: var(--color-gray-medium);
  font-weight: 400;
  margin-left: calc(var(--grid-unit) * 2);
}

.btn-primary, .btn-secondary {
  font-family: 'Inter', sans-serif;
  font-size: calc(var(--grid-unit) * 1.75);
  font-weight: 500;
  padding: calc(var(--grid-unit) * 1.5) calc(var(--grid-unit) * 3);
  border: 1px solid var(--color-black);
  background: var(--color-black);
  color: var(--color-white);
  cursor: pointer;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  transition: all 0.2s ease;
  display: inline-block;
}

.btn-primary:hover {
  background: var(--color-white);
  color: var(--color-black);
}

.btn-secondary {
  background: var(--color-white);
  color: var(--color-black);
}

.btn-secondary:hover {
  background: var(--color-black);
  color: var(--color-white);
}

.wardrobe-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: calc(var(--grid-unit) * 3);
  margin-bottom: calc(var(--grid-unit) * 4);
}

@media (min-width: 768px) {
  .wardrobe-grid {
    grid-template-columns: repeat(4, 1fr);
    gap: calc(var(--grid-unit) * 4);
  }
}

@media (min-width: 1280px) {
  .wardrobe-grid {
    grid-template-columns: repeat(4, 1fr);
  }
}

.wardrobe-item {
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  background: var(--color-white);
}

.wardrobe-item:hover .item-image {
  opacity: 0.95;
}

.wardrobe-item:hover .analyze-button {
  opacity: 1;
  visibility: visible;
}

.item-image-container {
  position: relative;
  width: 100%;
  aspect-ratio: 3/4;
  overflow: hidden;
  /* border: 1px solid var(--color-border); */
  background: #FAFAFA;
}

.item-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: opacity 0.3s ease;
}

.analyze-button {
  position: absolute;
  bottom: calc(var(--grid-unit) * 2);
  left: 50%;
  transform: translateX(-50%);
  background: var(--color-black);
  color: var(--color-white);
  padding: calc(var(--grid-unit) * 1.5) calc(var(--grid-unit) * 2);
  font-size: calc(var(--grid-unit) * 1.5);
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  border: none;
  cursor: pointer;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s ease;
  white-space: nowrap;
  font-family: 'Inter', sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 4px;
}

.analyze-button:hover {
  background: var(--color-gray-dark);
}

.item-name {
  margin-top: calc(var(--grid-unit) * 1.5);
  font-size: calc(var(--grid-unit) * 1.75);
  font-weight: 400;
  color: var(--color-black);
  text-align: center;
  line-height: 1.4;
  padding: 0 calc(var(--grid-unit) * 1);
}

.quality-badge {
  position: absolute;
  top: calc(var(--grid-unit) * 1);
  right: calc(var(--grid-unit) * 1);
  padding: calc(var(--grid-unit) * 0.5) calc(var(--grid-unit) * 1);
  background: var(--color-white);
  border: 1px solid var(--color-black);
  font-size: calc(var(--grid-unit) * 1.5);
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

.status-indicator {
  position: absolute;
  top: calc(var(--grid-unit) * 1);
  left: calc(var(--grid-unit) * 1);
  width: calc(var(--grid-unit) * 1);
  height: calc(var(--grid-unit) * 1);
  border-radius: 50%;
}

.status-saved {
  background: #22C55E;
}

.status-pending {
  background: #FFC107;
  animation: pulse 2s cubic-bezier(0.4, 0, 0.6, 1) infinite;
}

@keyframes pulse {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.5; }
}

.loading-placeholder {
  position: relative;
  background: var(--color-white);
  border: 1px solid var(--color-border);
  aspect-ratio: 3/4;
}

.loading-content {
  position: absolute;
  inset: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: rgba(255, 255, 255, 0.95);
  padding: calc(var(--grid-unit) * 2);
}

.loading-spinner {
  width: calc(var(--grid-unit) * 4);
  height: calc(var(--grid-unit) * 4);
  border: 2px solid var(--color-gray-light);
  border-top-color: var(--color-black);
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

.loading-text {
  font-size: calc(var(--grid-unit) * 1.5);
  color: var(--color-gray-medium);
  margin-top: calc(var(--grid-unit) * 1);
  text-align: center;
}


.controls {
  position: fixed;
  bottom: 3%;
  left: 38%;
  width: 316px;
  background:#000;
  padding: 0 10px;
  z-index: 1;
  border-radius: 2px
}
.controls{
  color:#fff;
}